import {Button, Container ,Navbar} from 'react-bootstrap';
import jss from 'jss'
import camelCase from 'jss-plugin-camel-case'
jss.use(camelCase())

let styles = {
    throwBtn: {
        fontWeight: "bold",
        textTransform: "uppercase",
        minHeight: "5rem"
    },
    throwIndicator: {
        backgroundColor: "#C65600",
        padding: "0.25rem 0.85rem",
        borderRadius: "1.5rem",
        color: "#ffc107"
    }
}
export default function BottomBar(props: any) {
    let {classes} = jss.createStyleSheet(styles).attach()
    return (<Navbar fixed="bottom">
                <div className="w-100 d-grid gap-2 px-2">
                    <Button 
                        variant="warning" 
                        size="lg" 
                        className={"shadow-sm d-flex justify-content-between align-items-center "+classes.throwBtn}
                        onClick={() =>props.getThrow()}
                        disabled={(props.turn >= 3)}
                    >
                       Throw <span className={classes.throwIndicator}>{3-props.turn}</span>
                    </Button>
                </div>
            </Navbar>)
}