import {Container,Navbar} from 'react-bootstrap';
import jss from 'jss'
import camelCase from 'jss-plugin-camel-case'
jss.use(camelCase())

let styles = {
    navbar: {
        backgroundColor: '#ffc107',
        boxShadow: "rgba(0, 0, 0, 0.24) 0px 3px 8px",
    },
    pointCounter: {
        border: "1px solid ",
        fontWeight: "bold",
        fontSize: "1.2rem",
        padding: "0.5rem 1rem",
        borderRadius: "0.5rem",
    },
    roundCounter: {
      fontSize: "1.2rem",
      textTransform: "uppercase",
    }
}
export default function TopBar(props: any) {
    let {classes} = jss.createStyleSheet(styles).attach()
    return (<Navbar className={classes.navbar}>
        <Container>
          <Navbar.Text className={classes.roundCounter +" justify-content-start fw-bold"}>
            Round n {props.round}
          </Navbar.Text>
          <Navbar.Text className={"shadow-sm justify-content-end "+classes.pointCounter}>
            {props.total}
          </Navbar.Text>
        </Container>
      </Navbar>)
}